.sl_container {
       bottom: 0;
       left: 0;
       opacity: 1;
       position: fixed;
       right: 0;
       top: 0;
       z-index: 999;
       background-color: #000;
}

.sl_wrap {
       height: 100%;
       z-index: 2;
       position: relative;
}

.sl_wrap_row {
       display: flex;
       flex-wrap: wrap;
       align-items: center;
       justify-content: center;
       height: 100%;
       width: 100%;
}

.sl_media_,
.sl_content_wrap {
       -ms-flex-preferred-size: 0;
       flex-basis: 0;
       -webkit-box-flex: 1;
       -ms-flex-positive: 1;
       flex-grow: 1;
       max-width: 100%;
       height: 100vh;
}

.sl_media_wrap{
       display: flex;
       align-items: center;
       justify-content: center;
       width: 100%;
       opacity: 0;
       animation: tb_scale_animation_rev .8s ease forwards;
}

.sl_media_wrap.sl_media_ani{
       display: flex;
       opacity: 1;
       animation: tb_scale_animation .8s ease forwards;
}

@keyframes tb_scale_animation {
       0%   { 
              transform: scale(0.5); 
              opacity: 0;
       }
       100% { 
              transform: scale(1);
              opacity: 1;
       }
}


.sl_media_ {
       display: flex;
       align-items: center;
       height: 100%;
}

.sl_media_wrap_{
       position: relative;
       width: 100%;
}

.sl_media_{
       position: absolute;
       left: 0;
       top: 0;
       width: 100%;
       height: 100%;
       object-fit: cover;
       object-position: center;
}

.sl_content_wrap {
       display: flex;
       justify-content: center;
       align-items: center;
       max-width: 40%;
       height: 100%;
       background: rgba(0, 0, 0, 1);
}

.sl_only_media .sl_media_,
.sl_only_text .sl_content_wrap {
       max-width: 100%;
}

.sl_content_in {
       padding: 5%;
}

.sl_con_line-3 {
       -webkit-line-clamp: 3;
}

.sl_con_line-4 {
       -webkit-line-clamp: 4;
}

.sl_con_line-5 {
       -webkit-line-clamp: 5;
}

.sl_con_line-6 {
       -webkit-line-clamp: 6;
}

.sl_con_line-7 {
       -webkit-line-clamp: 7;
}

.sl_con_line-8 {
       -webkit-line-clamp: 8;
}

.sl_con_line-9 {
       -webkit-line-clamp: 9;
}

.sl_con_line-10 {
       -webkit-line-clamp: 10;
}

.sl_img_ {
       display: block;
       max-width: 100%;
       margin: 0 auto;
       height: auto;
}

.sl_overlay_:before {
       background: rgba(0, 0, 0, 0.5);
       -webkit-backdrop-filter: blur(12px);
       backdrop-filter: blur(12px);
       position: absolute;
       content: "";
       width: 100%;
       height: 100%;
       left: 0;
       top: 0;
}

.sl_overlay_ {
       background: rgba(0, 0, 0, 1);
       position: absolute;
       left: 0;
       bottom: 0;
       width: 100%;
       height: 100%;
       background-size: cover;
       background-position: center;
       background-repeat: no-repeat;
       z-index: 0;
       opacity: 0;
       transition: opacity .5s ease-in;
}

.sl_overlay_.sl_media_ani{
       opacity: 1;
}

.sl_media_ .fb_iframe_widget_fluid span {
       text-align: center;
       height: 100% !important;
       display: flex;
       align-items: center;
       justify-content: center;
}

