//        position: static !important;
//        //     height: calc(100vh - 20px) !important;
//        width: auto !important;
//        width: 46vw !important;
// }

.sl_media_ img {
       max-width: 100%;
       height: 100%;
}


.sl_author {
       width: calc(100% - 30px);
}

.sl_author_profile {
       height: 44px;
       width: 44px;
       border-radius: 50%;
       margin-right: 10px;
       background-repeat: no-repeat;
       background-size: cover;
       background-position: center;
       display: flex;
       flex-shrink: 0;
       overflow: hidden;
}

.sl_author_profile__ {
       height: 100% !important;
       width: 100% !important;
       margin: 0;
       object-fit: cover;
       object-position: center;
}

.sl_authorname {
       font-size: 14px;
       font-weight: 600;
       margin-bottom: 4px;
       text-align: left;
       width: 100%;
       white-space: nowrap;
       display: block;
       overflow: hidden;
       text-overflow: ellipsis;
       line-height: normal;
}

.sl_authorname {
       font-family: var(--tb-author-font);
       color: var(--tb-author-color);
}

.sl_username,
.sl_time {
       font-family: var(--tb-font);
       color: var(--tb-author-color);
}

.sl_seprator {
       background-color: var(--tb-author-color);
}

.sl_content_ {
       font-size: calc(12px + .8vw);
       text-align: center;
       line-height: 1.6;
       min-height: calc(3vh + 1vw);
       word-break: break-word;
       color: var(--tb-color);
       display: -webkit-box;   
       -webkit-box-orient: vertical;     
       overflow: hidden; 
}

.sl_content {
       font-size: calc(15px + 0.3vh + 0.3vw);
       line-height: 1.6;
       text-align: center;
       display: -webkit-box;
       -webkit-box-orient: vertical;
       overflow: hidden;
       color: #fff;
}
.sl_author {
       display: flex;
       align-items: center;
       flex-grow: 1;
}

.sl_author_wrapper {
       display: flex;
       align-items: center;
       margin-bottom: 30px;
}

.sl_author_info {
       overflow: hidden;
       display: flex;
       flex-shrink: 0;
       flex-direction: column;
       width: calc(100% - 54px);
       padding-right: 10px;
       align-items: flex-start;
}


.sl_post_info {
       display: flex;
       align-items: center;
       width: 100%;
}

.sl_seprator {
       height: 4px;
       width: 4px;
       min-width: 4px;
       border-radius: 50%;
       margin: 0 6px;
       opacity: 0.8;
       display: flex;
       flex-shrink: 1;
}

.sl_username,
.sl_time {
       font-size: 10px;
       font-weight: 500;
       opacity: 0.8;
       line-height: 1.5;
       overflow: hidden;
       line-height: normal;
       white-space: nowrap;
       text-overflow: ellipsis;
}

.sl_username {
       max-width: 65%;
}

.sl_time {
       max-width: 35%;
}

.sl_social_ {
       font-size: 24px;
       margin: 5px 0;
       color: var(--tb-icon-color);
}

.sl_social_center_ico {
       font-size: 24px;
}


@media screen and (max-width:600px) {
       .sl_media_ .fb_iframe_widget iframe {
              width: 100vw !important;
       }

}

.sl_only_media .sl_media_ {
       width: 100%;
}

//    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);


@media (orientation: portrait) {
       .sl_wrap_row {
              flex-direction: column;
       }

       .sl_content_wrap {
              width: 100%;
              max-width: 100%;
       }


       .sl_only_media .sl_content_wrap{
              height: 100vh;
       }
}